.NavBar {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background: linear-gradient(
    to bottom,
    rgb(244, 252, 254, 0.8),
    rgb(244, 252, 254, 0)
  );
  z-index: 1000;
  padding: 10px;
}
.NavBar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.NavBar li {
  margin: 0 30px;
}

.NavBar a {
  text-decoration: none;
  color: black;
  font-family: Arial, sans-serif;
  font-weight: 300;
  font-size: 18px;
}

.NavBar a:hover {
  text-decoration: underline;
}
.home-icon {
  height: 80px; /* Adjust height as needed */
  width: auto;
  margin-right: 300px; /* Add space between image and nav items */
}
